@use "../global";

.herotitle-container {
  &.blogtop {
    padding-top: 41.65%;
    .hero-content {
      top: 50%;
      transform: translate(-50%,-50%);
      @include global.mq(xxl) {
        transform: translate(0,-50%);
      }
      @include global.mq(md) {
        width: auto;
        top: auto;
        bottom: 15px;
        left: 0;
        transform: translate(0,0);
        padding-left: 15px;
        padding-right: 15px;
      }
    }
    @include global.mq(md) {
      padding-top: 70%;
    }
  }
}

.blog-category-select {
  margin-top: 64px;
  font-size: 1.4rem;
  line-height: 1;
  @include global.flex(nowrap,flex-start,center);
  span {
    font-weight: bold;
    white-space: nowrap;
    margin-right: 12px;
    @include global.mq(md) {
      font-size: 1rem;
    }
  }
  .select-area {
    flex: 1;
    .uk-select {
      width: auto;
      white-space: nowrap;
      padding: 0 3em 0 1em;
      font-weight: bold;
      @include global.mq(md) {
        font-size: 1.6rem;
        transform: scale(0.6);
      }
      option {
        padding: 0;
      }
    }
  }
  @include global.mq(md) {
    margin-top: 15px;
  }
}

.blog-card-container {
  margin-top: 32px;
  @include global.flex(wrap,flex-start,stretch);
  margin-left: -12px;
  margin-right: -12px;
  @include global.mq(md) {
    margin-top: 15px;
    margin-left: -5px;
    margin-right: -5px;
  }
  .card {
    box-sizing: border-box;
    padding-left: 12px;
    padding-right: 12px;
    flex-basis: 25%;
    margin-bottom: 24px;
    .card-img {
      padding-top: 66.666666666666667%;
    }
    &:nth-of-type(1) {
      transition: transform 0.7s ease-in;
    }
    time {
      display: block;
      line-height: 1;
      margin-bottom: 12px;
      font-family: Helvetica, sans-serif;
    }
    p {
      font-weight: bold;
    }
    @include global.mq(md) {
      padding-left: 5px;
      padding-right: 5px;
      flex-basis: 50%;
      margin-bottom: 24px;
      h3 {
        margin-bottom: 0;
      }
    }
  }
}

.blog-head {
  box-sizing: border-box;
  padding: 64px 32px;
  width: 720px;
  margin-left: auto;
  margin-right: auto;
  @include global.mq(md) {
    width: auto;
    padding: 15px 0;
    font-size: 1.1rem;
  }
  time {
    display: block;
    margin-bottom: 12px;
    font-size: 1.3rem;
    line-height: 1;
    font-weight: bold;
    font-family: Helvetica, sans-serif;
  }
  h1 {
    font-size: 3.3rem;
    line-height: 1.4;
    font-weight: bold;
    @include global.mq(md) {
      font-size: 2rem;
    }
  }
  > img {
    display: block;
    width: 100%;
    margin-bottom: 32px;
    @include global.mq(md) {
      margin-bottom: 16px;
    }
  }
  figure {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-right: 32px;
    img {
      border-radius: 100%;
      overflow: hidden;
      width: 60px;
      height: 60px;
      display: block;
      box-shadow: 0px 15px 45px -9px rgba(0,0,0,.2);
      @include global.mq(md) {
        width: 30px;
        height: 30px;
      }
    }
    figcaption {
      white-space: nowrap;
      margin-left: 12px;
      font-size: 1.4rem;
      font-weight: bold;
      @include global.mq(md) {
        font-size: 1rem;
      }
    }
  }
  .blog-categories {
    margin-top: 24px;
    ul {
      @include global.flex(wrap,flex-start,center);
      li {
        margin-right: 16px;
        &:last-child {
          margin-right: 0;
        }
        a {
          font-size: 1.3rem;
          font-weight: 500;
          text-decoration: underline;
          @include global.mq(md) {
            font-size: 1rem;
          }
        }
      }
    }
  }
}

.blog-meta {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 32px;
  @include global.mq(md) {
    margin-bottom: 16px;
  }
  .socials {
    li {
      margin-left: 12px;
    }
  }
}

.blog-body {
  box-sizing: border-box;
  padding: 0 32px;
  width: 720px;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.7rem;
  @include global.mq(md) {
    width: auto;
    padding: 15px 0;
    font-size: 1.3rem;
  }
  h2,
  h3,
  h4,
  h5,
  blockquote,
  img,
  .iframe,
  table,
  ul,
  ol,
  p {
    margin-bottom: 32px;
    @include global.mq(md) {
      margin-bottom: 16px;
    }
  }
  h2,
  h3,
  h4,
  h5 {
    font-weight: bold;
  }
  h2 {
    font-size: 2.5rem;
    @include global.mq(md) {
      font-size: 2rem;
    }
  }
  h3 {
    font-size: 2.2rem;
    @include global.mq(md) {
      font-size: 1.8rem;
    }
  }
  .iframe {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
  }
  blockquote {
    border-left: 5px solid #CCC;
    padding-left: 15px;
    color: global.$black;
    p:last-of-type {
      margin-bottom: 0;
    }
  }
  .iframe iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  ul,ol {
    margin-left: 20px;
    li {
      list-style-position: outside;
    }
  }
  ol {
    li {
      list-style-type: decimal;
    }
  }
  ul {
    li {
      list-style-type: square;
    }
  }
  table {
    width: 100%;
    table-layout: auto;
    border-collapse: collapse;
    border: 1px solid #CCC;
    th {
      font-weight: bold;
    }
    td,
    th {
      border: 1px solid #CCC;
      font-size: 1.3rem;
      background-color: rgba(255,255,255,0.5);
      padding: 12px;
      @include global.mq(md) {
        font-size: 1rem;
        padding: 6px;
      }
    }
  }
}

.relative-blog {
  padding-top: 64px;
  padding-left: 64px;
  padding-right: 64px;
  padding-bottom: 32px;
  background-color: #FFF;
  @include global.mq(md) {
    padding: 24px 15px;
  }
  .blog-card-container {
    margin-top: 0;
  }
  h2 {
    text-align: center;
    font-weight: bold;
    font-size: 2rem;
    line-height: 1;
    margin-bottom: 32px;
    @include global.mq(md) {
      font-size: 1.4rem;
      margin-bottom: 16px;
    }
  }
  .more {
    padding-top: 64px;
    padding-bottom: 64px;
    text-align: center;
    @include global.mq(md) {
      padding-top: 32px;
      padding-bottom: 32px;
    }
    a {
      font-size: 1.4rem;
      background-color: #666;
      padding-top: .6em;
      padding-bottom: .6em;
      border-radius: 20px;
      line-height: 1;
      color: #FFF;
      text-transform: none;
      @include global.mq(md) {
        font-size: 1.2rem;
        display: block;
        border-radius: 0;
        padding-top: 1em;
        padding-bottom: 1em;
      }
    }
  }
}
